import { Injectable } from '@angular/core';
import { ComposerService } from '@placeos/composer';
import { first } from 'rxjs/operators';

import { BaseAPIService } from '../base.service';
import { ISpaceAvailabilityOptions, Space } from './space.class';
import { ApplicationLoadingState, HashMap } from '@mckinsey-converge/base';
import { availabilityOptionsToQuery } from './space.utilities';
import { ServiceManager } from '../service-manager.class';
import { Building, OrganisationService } from '../organisation';
import { ApplicationService } from '../app.service';
import { SettingsService } from '../settings.service';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';
import { Observable } from 'rxjs';
import {  User  } from '@mckinsey-converge/data-common';

let SPACE_LIST = [];

export function findSpace(id: string) {
    return SPACE_LIST.find((_) => _.id === id || _.email === id);
}

export interface SpaceRuleDataForRecurrentBooking {
    is_recurrent: boolean;
    duration: number;
    user?: User;
    buildings?: Building[],
    dateTz: DateTZ
}

@Injectable({
    providedIn: 'root',
})
export class SpacesService extends BaseAPIService<Space> {
    
    public optionsForRuleCheck: SpaceRuleDataForRecurrentBooking = {
        is_recurrent: false,
        duration: null,
        user: null,
        buildings: [],
        dateTz: null
    };

    constructor(
        protected _composer: ComposerService,
        private _org: OrganisationService,
        private _service: ApplicationService,
        settingsService: SettingsService
    ) {
        
        super(_composer, settingsService);
        ServiceManager.setService(Space, this);
        this._name = 'Space';
        this._api_route = 'rooms';
        this._compare = (a, b) =>
            !a.id.localeCompare(b.id) || !a.email.localeCompare(b.email);
        this._list_filter = (a: Space) => {
            const bld = this._org.building;
            return a.level.building_id === bld.id;
        };
        this._org.initialised
            .pipe(first((_) => _))
            .subscribe(() => this.init());
    }

    public async query(query: HashMap = {}, setList:boolean = false) {
        let list = await super.query(query);

        // Update the available occurence for each space as per office rule
        if(Object.keys(query).length > 1 && !setList && !!query.recurrence_period) {
            list = this.updateAvailableOccurencePerRule(list);
        }

        if ( (query.hasOwnProperty('cache') && query.cache === false) || !Object.keys(query).length || setList) {
            this.set('list', list);
            SPACE_LIST = list;
        }
        return list;
    }

    public filterAvailableSpaces(list: Space[]) {
        return list.filter((i) => {
            if (i.recurrence_availability?.length) return i; //if recurrence, send the space regardless of availability (needed for edit and cloning series)
            return i.available;
        });
    }

    /**
     * Get available spaces
     * @param options
     */
    public available(
        options: ISpaceAvailabilityOptions,
        uniqueId?: string,
        filter: boolean = true
    ): Promise<Space[]> {
        if (!options) {
            throw new Error('Space avilability requires request options');
        }

        if(options.is_recurrent) {
            this.optionsForRuleCheck = {
                is_recurrent: options.is_recurrent,
                duration: options.duration,
                user:this._org.user,
                buildings: this._org.buildings,
                dateTz: options.dateTz
            }
        }

        const now = DateNow(new Date());
        if (!options.dateTz) {
            options.dateTz = now.startOfValue('minute');
        }

        if (options.hide_declined === undefined) {
            options.hide_declined = true;
        }

        const key = `available|${options.id ? options.id : ''}|${
            uniqueId ?? ''
        }`;
        if (!this._promises[key]) {
            this._promises[key] = new Promise((resolve, reject) => {
                const respond = (list: Space[]) => {
                    delete this._promises[key];
                    resolve(filter ? this.filterAvailableSpaces(list) : list);
                };
                const error = (e) => {
                    reject(e);
                    delete this._promises[key];
                };
                const query = availabilityOptionsToQuery(options);
                if (options.id) {
                    this.show(options.id, query).then(
                        (i) => respond([i]),
                        error
                    );
                } else {
                    this.query(query).then(respond, error);
                }
            });
        }

        return this._promises[key];
    }

    /**
     * Load initial data for the service
     */
    protected async load(): Promise<void> {
        const loading: ApplicationLoadingState =
            this._service.get('loading') || {};
        if (!loading.spaces) {
            loading.spaces = {
                message: 'Loading space data',
                state: 'loading',
            };
            this._service.set('loading', loading);
        }
        // Adjusted the query by incorporating the "zone_ids" option to filter rooms based on specific zone IDs instead of considering all thousands of rooms.
        const option = {
            zone_ids: this._org.building.id
        }
        await this.query(option, true).catch(() => {
            loading.spaces = { message: 'Loading space data', state: 'failed' };
            this._service.set('loading', loading);
        });
        loading.spaces = { message: 'Loading space data', state: 'complete' };
        this._service.set('loading', loading);
    }

    /**
     * Convert raw data into API object
     * @param raw_data Raw API data
     */
    public process(raw_data: HashMap): Space {
        return new Space(raw_data);
    }
    /**
     * 
     * Special observable function to make http call for rooms
     * @param query 
     * @returns observable
     */
    public queryRooms = (query: HashMap = {}): Observable<HashMap> => super.queryRoomsForQR(query);

    public setSpaceList(list) {
        this.set('list', list);
        SPACE_LIST = list;
    }

    public queryBooking = (query: HashMap = {}) : Observable<Space[]> | Observable<any>  => super.queryObsr(query);
    public updateSpace = (id: string, form_data: HashMap, should_inject_concierge: Boolean = false, query_params: HashMap = {}) : Observable<Space[]> | Observable<any>  => super.updateObsr(id, form_data, should_inject_concierge, query_params);
    public uploadSpacePhotos = (apiSubRoute: string, form_data: HashMap, should_inject_concierge: Boolean = false, query_params: HashMap = {}) : Observable<any> | Observable<any>  => super.uploadSpacePhotos(apiSubRoute, form_data, should_inject_concierge, query_params);
    
    public updateRoomList() {
        this.load().then((_) => null)
    }

    /**
     * 
     * @param list 
     * @returns list: Space[]
     */
    public updateAvailableOccurencePerRule(list: Space[]) {
        
        return list.map(space => {
            if(this.optionsForRuleCheck.is_recurrent && space.settings?.recurrence_availability?.length) {
                const recurrence_availability = space.settings?.recurrence_availability.map((occurrence) => {
                    // Convert the requested date to building timezone
                    const requestDateTz = new DateTZ({ date: occurrence?.date*1000, is_local_tz: false, building_tz: this.optionsForRuleCheck.dateTz.building_tz });
                    if( occurrence.available === true ){
                        // Check the availability of the room for the requested date and time
                        const isSpaceAvailablePerRule = space.rulesFor({
                            dateTz: requestDateTz,
                            duration: this.optionsForRuleCheck.duration,
                            host: this._org.user,
                        });
                        return {
                                ...occurrence,
                                available: !isSpaceAvailablePerRule.hide,
                                reason: isSpaceAvailablePerRule.reason        
                            };
                    }
                    return occurrence;
                });
                // Update the space's availableOccurencesPerRule property
                space.updateRecurringAvailability(recurrence_availability);
            }
            return space;
        });

    }
}
