import { Component, Input, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { setRecurrenceStartsTime } from '@mckinsey-converge/data-common';
import { BookingFlowEvent } from '../booking-modal.component';
import { MatDialog } from '@angular/material/dialog';
import { DateTime } from 'luxon';
import { CateringDropComponent } from '@mckinsey-converge/ui';
import { ChangeDetectorRef } from '@angular/core';

@Component({
    selector: 'booking-spaces-form',
    templateUrl: './form.component.html',
    styleUrls: ['./form.component.scss']
})
export class BookingSpaceFlowFormComponent {
    /** Booking form fields */
    @Input() public form: FormGroup;
    /** Whether form is not the first page in the flow */
    @Input() public can_return: boolean = false;
    /** Emitter for flow update events */
    @Output() public event = new EventEmitter<BookingFlowEvent>();

    @ViewChild('container', { static: true }) private container: ElementRef<HTMLDivElement>;

    constructor(private _dialog: MatDialog, private cdRef: ChangeDetectorRef) {}

    /** Move flow to next step */
    public next(): void {
        if (this.form.controls?.is_recurrent?.value) {
            setRecurrenceStartsTime(this.form);
        }
    
        this.form.markAllAsTouched();
        this.form.updateValueAndValidity();
    
        if (this.form.controls?.attendees?.value?.some(item => item.name.length > 50)) {
            const attendeesControl = this.form.get('attendees');
            attendeesControl.setErrors({
                customError: 'Attendee name cannot be more than 50 characters',
            });
        }
    
        // Check if booking is outside catering hours and 'Need catering?' is selected
        const isOutsideCateringHours = this.checkIfOutsideCateringHours();
        const hasCatering = this.form.controls?.has_catering?.value;
        const cateringOrders = this.form.controls?.catering?.value;
    
        if (isOutsideCateringHours && hasCatering && cateringOrders && cateringOrders.length > 0) {
            // Open the dialog
            const dialogRef = this._dialog.open(CateringDropComponent, {
                autoFocus: false,
                backdropClass: 'mck-modal-backdrop',
        });
    
            dialogRef.afterClosed().subscribe(result => {
                if (result) {
                    // User confirmed to remove catering
                    this.form.controls.has_catering.setValue(false);
                    this.form.controls.catering?.setValue([]);
                    // Update validity
                    this.form.updateValueAndValidity();
                    // Proceed to next step if form is valid
                    if (this.form.valid) {
                        this.event.emit({ type: 'next', step: 'form' });
                    }
                } else {
                    // User chose to go back, do not proceed
                    // Optionally, scroll to the relevant field
                    if (this.container) {
                        this.container.nativeElement.scrollIntoView({
                            behavior: 'smooth',
                            block: 'start',
                            inline: 'nearest'
                        });
                    }
                }
            });
        } else {
            // Proceed as usual
            if (this.form.valid) {
                this.event.emit({ type: 'next', step: 'form' });
            } else if (this.container) {
                this.container.nativeElement.scrollIntoView({
                    behavior: 'smooth',
                    block: 'start',
                    inline: 'nearest'
                });
            }
        }
    }
    

    private checkIfOutsideCateringHours(): boolean {
        const dateTz = this.form.controls?.dateTz?.value;
        const duration = this.form.controls?.duration?.value;
    
        if (dateTz && duration) {
            // Create DateTime object for the start time
            const startDateTime = DateTime.fromISO(dateTz.date, { zone: dateTz.building_tz });
    
            // Calculate the end time
            const endDateTime = startDateTime.plus({ minutes: duration });
    
            // Get the date part of the startDateTime
            const bookingDate = startDateTime.startOf('day');
    
            // Define 7 am and 8 pm on the booking date
            const dayStartAt7am = bookingDate.set({ hour: 7 });
            const dayEndAt8pm = bookingDate.set({ hour: 20 });
    
            // Check if booking ends before or at 7 am, or starts at or after 8 pm
            if (endDateTime <= dayStartAt7am || startDateTime >= dayEndAt8pm) {
                return true;
            } else {
                return false;
            }
        } else {
            // If date or duration is not set, assume not outside catering hours
            return false;
        }
    }
    
    /** Move flow to previous step */
    public previous(): void {
        this.event.emit({ type: 'previous', step: 'form' });
    }

    /** Reset form to initial state */
    public resetForm(): void {
        this.event.emit({ type: 'reset', step: 'form' });
    }
}
