import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'catering-drop-modal',
  templateUrl: './catering-drop-modal.component.html',
  styleUrls: ['./catering-drop-modal.component.scss']
})
export class CateringDropComponent {

  constructor(private dialogRef: MatDialogRef<CateringDropComponent>) {}

  onConfirm(): void {
    this.dialogRef.close(true);
}

onCancel(): void {
    this.dialogRef.close(false);
}
}
