import { Component, Input, SimpleChanges, OnChanges } from '@angular/core';
import { HashMap } from '@mckinsey-converge/base';
import { ReportDetails } from '../reports.component';



import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'newlineToBR',
  pure: true // Set pure to true to ensure it's a stateless pipe
})
export class NewlineToBRPipe implements PipeTransform {
  transform(value: string): string {
    if (typeof value !== 'string' || !value.includes('\n')) {
        // If the value is not a string or doesn't contain newline characters, return it as is
        return value;
      }
      return value.replace(/\n/g, '<br/>').replace(/\s/g, "\u00A0");
  }
}

@Component({
    selector: 'a-report-table',
    templateUrl: './report-table.component.html',
    styleUrls: ['./report-table.component.scss'],
})
export class ReportTableComponent implements OnChanges {
    /** List of report table data */
    @Input() public list: HashMap[] = [];
    /** Map of details for report */
    @Input() public details: ReportDetails;

    public fields: string[] = [];
    public sortField: string = '';
    public sortOrder: 'asc' | 'desc' = 'asc';

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.list && this.list && this.list.length) {
            this.fields = Object.keys(this.list[0]).filter(i => this.details.hide_fields.indexOf(i) < 0);
        }
    }

    public toggleSort(field: string) {
        if (this.isSortingDisabled(field)) {
            return; // Disable sorting for specific fields
        }

        if (this.sortField === field) {
            this.sortOrder = this.sortOrder === 'asc' ? 'desc' : 'asc';
        } else {
            this.sortField = field;
            this.sortOrder = 'asc';
        }

        this.sortTable(field);
    }

    private sortTable(field: string) {
        const sortedList = [...this.list].sort((a, b) => {
            let aValue = a[field];
            let bValue = b[field];

            const dateWithTimeRegex = /\d{1,2} \w{3,4} \d{4} -\s+\d{1,2}:\d{2} (AM|PM)/;

            if (
                typeof aValue === 'string' &&
                dateWithTimeRegex.test(aValue) &&
                typeof bValue === 'string' &&
                dateWithTimeRegex.test(bValue)
            ) {
                aValue = this.parseDateString(aValue);
                bValue = this.parseDateString(bValue);
            }

            if (aValue < bValue) {
                return this.sortOrder === 'asc' ? -1 : 1;
            } else if (aValue > bValue) {
                return this.sortOrder === 'asc' ? 1 : -1;
            } else {
                return 0;
            }
        });

        this.list = sortedList;
    }

    private parseDateString(dateString: string): Date {
        const [datePart, timePart] = dateString.split(' - ');
        const formattedDate = `${datePart} ${timePart}`;
        return new Date(formattedDate);
    }

    public isSortingDisabled(field: string): boolean {
        // Disable sorting for specific fields
        const disabledFields = ['recurr', 'cancelled_by', 'all_day'];
        return disabledFields.includes(field);
    }
}
