import { Component, OnInit, OnDestroy, ChangeDetectionStrategy, NgZone, ChangeDetectorRef, ViewChild } from '@angular/core';
import { first, map, take, takeWhile } from 'rxjs/operators';

import { ApplicationService, Booking, BookingStateService, BookingsPaginatedService, BookingsService, OrganisationService, User } from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';
import { UserStoreState, selectCurrentUser } from '@mckinsey-converge/user';
import { Store } from '@ngrx/store';
import * as cloneDeep from 'lodash/cloneDeep';
import { getListOfDateFormat, formatDateWithSuffix } from '@mckinsey-converge/base';
import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';
import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';
@Component({
	selector: 'day-view-approvals-view',
	templateUrl: './approvals-view.component.html',
	styleUrls: ['./approvals-view.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class DayViewApprovalsComponent extends BaseDirective implements OnInit, OnDestroy {
	/** List of spaces to check for pending approvals */
	// public space_list: string[] = [];
	/** Whether the pending bookings are being loaded */
	public loading: boolean = true;
	/** Whether an event action is in progress */
	// public waiting_on_event: boolean;
	/** Observable for tentative events */

	public inputText: string = '';

	public incommingRequests: Booking[] = [];
	public sortOrderByOptions: { display: string; value: string; meta: {}; }[];
	public selectedSort: { display: string; value: string; meta: {}; } = { display: 'Soonest booking', value: 'Soonest booking', meta: {} };

	public isClosed: boolean = false;
	public updatedRequest: Booking[] = [];

	/** Display value for the active date */
	// public get date_display(): string {
	// 	return 'Today';
	// }

	public currentUser: User;
	public update_requests_timer: any;
	private previousBookings: Booking[] = [];
	@ViewChild(CdkVirtualScrollViewport) viewport: CdkVirtualScrollViewport;
	private isInitialLoad: boolean = true;
	public highlightedBookingId: string | null = null;
	// New properties for tracking new incoming bookings
	private newRequestCount: number = 0;
	private initialPendingIds: Set<number> = new Set();
	private currentNotification: Notification | null = null;
	private originalFaviconUrl: string = '';

	constructor(
		private _org: OrganisationService,
		private _bookings_service: BookingsPaginatedService,
		private store: Store<UserStoreState>,
		private _bookings: BookingsService,
		private _booking_state_service: BookingStateService,
		private _zone: NgZone,
		private cdRef: ChangeDetectorRef,
		private _service: ApplicationService,) {
		super();
	}

	public ngOnInit(): void {
		this.subscription('currentUser', this.store.select(selectCurrentUser)
			.pipe(take(1))
			.subscribe((user: User) => {
				// Once we know we have the user loaded
				this.currentUser = new User(user);
				this.getHoldingBay();
			})
		);

		this.subscription(
			'building',
			this._org.listen('active_building').subscribe((_) => this.currentUser && this.getHoldingBay() )
		);

		this._zone.runOutsideAngular(() => {
			this.interval('holdingBay', () => {
				// update every 30 seconds.
				this.getHoldingBay();
			}, 30 * 1000);
		});

		this.sortOrderByOptions = [
			{ display: 'Soonest booking', value: 'soonest', meta: {} },
			{ display: 'Latest booking', value: 'latest', meta: {} },
			{ display: 'Newest request creation date', value: 'newest_date', meta: {} },
			{ display: 'Oldest request creation date', value: 'oldest_date', meta: {} }
		];

		this.selectedSort = { display: 'Soonest booking', value: 'Soonest booking', meta: {} };


		// this._service.get('CONCIERGE.holdingBay.update').subscribe(undo => {
			
		// })

		// update holding bay on booking update/undo decline
		this.subscription(
            'update',
            this._service
                ?.listen('CONCIERGE.holdingBay.update')
                ?.subscribe((event) => {
                    if(event) {
						// update holding bay after 2 seconds to avoid multiple requests 
						this.update_requests_timer = setTimeout(() => {
							this.getHoldingBay();
						}, 2000);
                    }
                })
        );

		if ('Notification' in window) {
			Notification.requestPermission().then(permission => {
				if (permission === 'granted') {
					console.log('Notification permission granted.');
				}
			});
		}

		const favicon = document.querySelector('link[rel="icon"]') as HTMLLinkElement | null;
		if (favicon) {
			this.originalFaviconUrl = favicon.href;
		}

	}

	public getHoldingBay() {
		if (!this._org.building || !this.currentUser) {
			return;
		}



		this.subscription('holdingBay', this._bookings_service.getHoldingBay(
			{
				'email': this.currentUser.email,
				'building_zone': this._org.building.id,
			}
			).subscribe((res) => {
				// Check if room object is missing 'room.building' for CNG-513
				const isMissingRoom = res?.results?.some(bkng => !bkng.room?.building?.name) || false;
				
				const hasInvalidEndDateInMasterBookings = res?.results
				?.filter(bkng => bkng.recurrence_type === 'master') 
				.some(bkng => {
				// Check for invalid endDateString for CNG-503
				  const date = new Date(bkng.endDateString);
				  return isNaN(date.getTime());
				}) || false;

				// Run updateRoomList() to get room info
				if (isMissingRoom || hasInvalidEndDateInMasterBookings) {
					// this._booking_state_service.updateRoomList();
				}
				
				const masterIds = [];
				const filterMasterBookings = res.results?.filter(bkng => {

					if(bkng.recurrence_type === 'master' ) {
						masterIds.push(bkng.recurrence_master_id);
					}
					return bkng.recurrence_type === null || bkng.recurrence_type === 'master';
				}) || [];

				const filterStandardBookings = res.results?.filter(bkng =>  bkng.recurrence_type === 'occurrence' 
				 	&& bkng.status === 'tentative' 
					&&  !masterIds.includes(bkng.recurrence_master_id) // edited from staff , need approval
				) || [];

				const filterStandardAndMasterBookings = [...filterMasterBookings, ...filterStandardBookings ];
				let expiredBkngs = [];
				const pendingBkngs = filterStandardAndMasterBookings?.filter(bkng => (bkng.timeBasedStatusLabel !== 'expired_yesterday') ? true : !expiredBkngs.push(bkng) ) || [];
				expiredBkngs.length && this.declineExpiredBookings(expiredBkngs);

				// Logic to track new booking requests and update notification count
				if (this.isInitialLoad) {
					pendingBkngs.forEach(bk => this.initialPendingIds.add(bk.id));
					this.isInitialLoad = false;
				}
				const newIncomingBookings = pendingBkngs.filter(bk => !this.initialPendingIds.has(bk.id));
				const newCount = newIncomingBookings.length;
				if (newCount !== this.newRequestCount) {
					this.newRequestCount = newCount;
					this.showOrUpdateNotification(newCount);
				}
				// Update the stored bookings for the next poll
				this.previousBookings = pendingBkngs;

				this.updatedRequest = cloneDeep(pendingBkngs);
				//  const request = cloneDeep(this.updatedRequest);
				this.searchRequest();
				this.loading = false;
				this.clearTimer();
				this.cdRef.detectChanges();

			},
			(error) => {
				console.log(error);
				this.incommingRequests = [];
				this.loading = false;
				this.cdRef.detectChanges();
			})
		);
	}

	public clearTimer() {
        this.update_requests_timer && clearTimeout(this.update_requests_timer);
    }

	/* istanbul ignore next */
	/**
	 * Track by function for ngFor optimisation
	 * @param index Index of the item
	 * @param item Data associated with the item at the given index
	 */
	public trackByFn(index: number, item: Booking) {
		return item.id || index;
	}

	/**
	 * store selected border by sorting method and apply to
	 * @param sortingValue
	 */
	public selectOrderBy(sortingValue: { display: string; value: string; meta: {}; }) {
		this.selectedSort = sortingValue;
		this.searchRequest();
	}

	/**
	 * Function apply order by sorting method to incomming requests for approval
	 * @param requests
	 */
	public applyOrderBy(requests: Booking[]): void {
		const items = cloneDeep(requests);
		switch (this.selectedSort.value) {
			case 'soonest':
				items.sort((a, b) => a.start_epoch - b.start_epoch );
				break;
			case 'latest':
				items.sort((a, b) => b.start_epoch - a.start_epoch );
				break
			case 'newest_date':
				items.sort((a, b) => b.created_epoch - a.created_epoch )
				break;
			case 'oldest_date':
				items.sort((a, b) => a.created_epoch - b.created_epoch )
				break;
			default:
				items.sort((a, b) => a.start_epoch - b.start_epoch );
				break;
		}

		this.incommingRequests = items;
		this.cdRef.detectChanges();
	}

	public totalCateringItems(booking: Booking) {
		let num = 0;
		booking.catering.forEach(el =>  el?.items.forEach(item => num += item.amount ) );
		return num;
	}

	public searchRequest(value?: string) {
		const searchText = this.inputText.toLowerCase();

			const filtered  = this.updatedRequest.filter( (el) => {
			const { room, organiser, booked_by, booker, booking_type, attendees, date_string, startDateTz, start_time, end_time, creationDateTz } = el;

			return room?.name?.toLowerCase().includes(searchText) ||            /// input text inlcudes room name
				room?.local_name?.toLowerCase().includes(searchText) ||            /// input text inlcudes room local name
				room?.street_and_city?.toLowerCase().includes(searchText) ||       /// input text inlcudes room address
				room?.building?.address?.toLowerCase().includes(searchText) ||     /// input text inlcudes room address
				room?.level.name?.toLowerCase().includes(searchText) ||              /// input text inlcudes room level

				organiser?.name?.toLowerCase().includes(searchText) ||               ///input text inlcudes host name
				(booked_by && booked_by['name']?.toLowerCase().includes(searchText)) ||             ///input text inlcudes booked by name
				(booker && booker['name']?.toLowerCase().includes(searchText)) ||                ///input text inlcudes booker name
				booking_type?.toLowerCase().includes(searchText)	||
				attendees?.some(att => att?.name?.toLowerCase().includes(searchText)) ||  ///input text inlcudes attendees name

				date_string?.toLowerCase().includes(searchText) ||                         /// date_string == '06 Mar 2023'
				formatDateWithSuffix(date_string).toLowerCase().includes(searchText) ||      /// date_string == '06th Mar 2023'
				formatDateWithSuffix(creationDateTz.formatDate('dd MMM yyyy')).toLowerCase().includes(searchText) ||      /// date_string == '06th Mar 2023'
				//searchText.includes(startDateTz?.date?.day?.toString()) ||   /// day 06/6
				//searchText.includes(startDateTz?.date?.monthLong) ||         /// monthLong == 'March'
				//searchText.includes(startDateTz?.date?.monthShort) ||        /// monthShort == 'Mar'
				searchText.includes(startDateTz?.date?.weekdayLong.toLowerCase()) ||       /// weekdayLong == 'Monday'
				searchText.includes(startDateTz?.date?.weekdayShort.toLowerCase()) ||        /// weekdayShort == 'Mon'


				getListOfDateFormat().some(format => startDateTz?.formatDate(format).toLowerCase().includes(searchText) ) ||
				getListOfDateFormat().some(format => creationDateTz?.formatDate(format).toLowerCase().includes(searchText) ) ||

				start_time?.toLowerCase().includes(searchText) ||
				end_time?.toLowerCase().includes(searchText)


			});

			this.applyOrderBy(filtered);
	}

	public declineExpiredBookings(expiredBkngs: Booking[] ) {
		// console.log(expiredBkngs);
		this._bookings.declineMultiple(expiredBkngs).subscribe(resArray => this.cdRef.detectChanges(),
		(error) => {
			console.log(error);
			this.cdRef.detectChanges();
		});

	}

	// Method to update notification with count.
	private showOrUpdateNotification(count: number) {
		if (!("Notification" in window) || Notification.permission !== "granted") {
			this.updateFaviconBadge(count);
			return;
		}
		if (this.currentNotification) {
			this.currentNotification.close();
			this.currentNotification = null;
		}
		if (count === 0) {
			this.updateFaviconBadge(0);
			return;
		}
		const message = count === 1
			? '1 meeting request is awaiting your approval.'
			: `${count} meeting requests are awaiting your approval.`;
		const options = {
			body: message,
			tag: 'bookingNotification',
			renotify: true
		};
		this.currentNotification = new Notification("New Room Booking Request", options);
		this.updateFaviconBadge(count);
		this.currentNotification.onclick = (event) => {
			event.preventDefault();
			window.focus();
			// Set the sorting order to 'newest_date'
			const newestSort = this.sortOrderByOptions.find(option => option.value === 'newest_date');
			if (newestSort) {
				this.selectOrderBy(newestSort);
			}
			// Scroll the virtual scroll viewport to the top
			if (this.viewport) {
				setTimeout(() => {
					this.viewport.scrollToIndex(0, 'smooth');
					// Highlight the top (latest) booking after scrolling
					if (this.incommingRequests && this.incommingRequests.length > 0) {
						this.highlightedBookingId = this.incommingRequests[0].id;
						this.cdRef.detectChanges();
						setTimeout(() => {
							this.highlightedBookingId = null;
							this.cdRef.detectChanges();
						}, 3000);
					}
				}, 10);
			}
		};
	}

	// Helper method to update the favicon with a badge showing the pending count
	private updateFaviconBadge(count: number): void {
		const favicon = document.querySelector('link[rel="icon"]') as HTMLLinkElement | null;
		if (!favicon) {
			return;
		}
		if (count === 0) {
			// Restore the original favicon
			favicon.href = this.originalFaviconUrl;
			return;
		}
		const img = new Image();
		img.src = this.originalFaviconUrl;
		img.onload = () => {
			const canvas = document.createElement('canvas');
			const size = 32;
			canvas.width = size;
			canvas.height = size;
			const ctx = canvas.getContext('2d');
			if (!ctx) { return; }
			// Original favicon as the background
			ctx.drawImage(img, 0, 0, size, size);

			const radius = 12;
			const centerX = size - radius + 2;
			const centerY = radius - 2;
			ctx.beginPath();
			ctx.arc(centerX, centerY, radius, 0, 2 * Math.PI);
			ctx.fillStyle = 'red';
			ctx.fill();
			// Here is the count as white text
			ctx.font = 'bold 16px sans-serif';
			ctx.fillStyle = 'white';
			ctx.textAlign = 'center';
			ctx.textBaseline = 'middle';
			ctx.fillText(count.toString(), centerX, centerY);

			// Update the favicon with the new image
			favicon.href = canvas.toDataURL('image/png');
		};
	}
}