import { Component, OnInit, Inject } from '@angular/core';
import { MAT_SNACK_BAR_DATA } from '@angular/material/snack-bar';

@Component({
    selector: 'custom-snackbar',
    templateUrl: './custom-snackbar-component.component.html',
    styleUrls: ['./custom-snackbar-component.component.scss'],
})
export class CustomSnackbarComponent implements OnInit {
    constructor(@Inject(MAT_SNACK_BAR_DATA) public data: any) {
    }

    ngOnInit() {}

    get getIcon() {
        switch (this.data.snackType) {
            case 'success':
                return {
                    type: this.data.snackType,
                    iconPath: 'assets/icon/toast-success.svg',
                };
            case 'error':
                return {
                    type: this.data.snackType,
                    iconPath: 'assets/icon/toast-failure.svg',
                };
            case 'warn':
                return {
                    type: this.data.snackType,
                    iconPath: 'assets/icon/toast-warning.svg',
                };
            case 'info':
                return {
                    type: this.data.snackType,
                    iconPath: 'assets/icon/toast-info.svg',
                };
            default:
                return { type: 'info', iconPath: 'assets/icon/toast-info.svg' };
        }
    }

    closeSnackbar() {
        this.data.snackBar.dismiss();
    }
}
